.stats {
  display: flex;
  justify-content: center;
  gap: 24px;
  padding: 40px 20px 80px;
  background: #f8fafc;
  flex-wrap: wrap;
}

.stat {
  background: white;
  border-radius: 12px;
  padding: 32px 40px;
  width: 220px;
  text-align: center;
  box-shadow: 0 4px 20px rgba(0,0,0,0.04);
  transition: transform 0.3s, box-shadow 0.3s;
}

.stat:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 30px rgba(0,0,0,0.08);
}

.stat svg {
  font-size: 28px;
  margin-bottom: 12px;
  color: #2563eb;
}

.stat strong {
  display: block;
  font-size: 26px;
  font-weight: 800;
  color: #0f172a;
  margin: 12px 0;
}

.stat span {
  font-size: 12px;
  color: #64748b;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}


@media (max-width: 1024px) {
  .stat {
    width: 200px;
    padding: 28px 32px;
  }

  .stat strong {
    font-size: 22px;
  }
}


@media (max-width: 768px) {
  .stats {
    padding: 40px 20px 60px;
    gap: 16px;
  }

  .stat {
    width: calc(50% - 8px);
    padding: 24px 20px;
  }

  .stat svg {
    font-size: 24px;
  }

  .stat strong {
    font-size: 20px;
  }

  .stat span {
    font-size: 11px;
  }
}

@media (max-width: 480px) {
  .stats {
    padding: 30px 16px 50px;
    gap: 12px;
  }

  .stat {
    width: 100%;
    padding: 20px 16px;
  }

  .stat svg {
    font-size: 22px;
    margin-bottom: 8px;
  }

  .stat strong {
    font-size: 18px;
    margin: 10px 0;
  }

  .stat span {
    font-size: 10px;
  }
}