.footer {
  background: #0b1220;
  color: white;
  padding: 60px;
}

.cols {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 40px;
  margin-bottom: 40px;
}

.cols h3 {
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 16px;
}

.cols h4 {
  font-size: 16px;
  font-weight: 700;
  margin-bottom: 12px;
  color: white;
}

.cols p {
  font-size: 14px;
  line-height: 1.6;
  color: #9ca3af;
}

.footer a {
  display: block;
  color: #9ca3af;
  margin-bottom: 8px;
  font-size: 14px;
  transition: color 0.3s;
  cursor: pointer;
}

.footer a:hover {
  color: white;
}

.bottom {
  margin-top: 40px;
  border-top: 1px solid #1f2937;
  padding-top: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.bottom span {
  font-size: 13px;
  color: #9ca3af;
}

.bottom div {
  display: flex;
  gap: 16px;
  font-size: 20px;
  color: #9ca3af;
}

.bottom a {
  color: #9ca3af;
  transition: color 0.3s;
  cursor: pointer;
}

.bottom a:hover {
  color: white;
}

@media (max-width: 1024px) {
  .footer {
    padding: 50px 40px;
  }

  .cols {
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
  }
}

@media (max-width: 768px) {
  .footer {
    padding: 40px 20px;
    text-align: center;
  }

  .cols {
    grid-template-columns: 1fr;
    gap: 30px;
  }

  .cols h3 {
    font-size: 16px;
  }

  .cols h4 {
    font-size: 14px;
    margin-top: 16px;
  }

  .cols p {
    font-size: 13px;
  }

  .footer a {
    font-size: 13px;
  }

  .bottom {
    flex-direction: column;
    gap: 20px;
    text-align: center;
  }

  .bottom span {
    width: 100%;
  }

  .bottom div {
    justify-content: center;
    gap: 20px;
  }
}

@media (max-width: 480px) {
  .footer {
    padding: 30px 16px;
  }

  .cols {
    gap: 24px;
  }

  .cols h3 {
    font-size: 14px;
  }

  .cols h4 {
    font-size: 13px;
  }

  .cols p {
    font-size: 12px;
  }

  .footer a {
    font-size: 12px;
    margin-bottom: 6px;
  }

  .bottom {
    gap: 16px;
    padding-top: 16px;
  }

  .bottom span {
    font-size: 11px;
  }

  .bottom div {
    gap: 16px;
    font-size: 18px;
  }
}