.donation {
  background: linear-gradient(135deg, #2563eb, #1d4ed8);
  color: white;
  text-align: center;
  padding: 100px 20px;
}

.donation h2 {
  font-size: 32px;
  font-weight: 800;
  margin-bottom: 12px;
}

.donation p {
  font-size: 16px;
  margin-bottom: 40px;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.donation-box {
  background: white;
  color: black;
  max-width: 420px;
  margin: 40px auto 0;
  padding: 32px;
  border-radius: 16px;
}

.amounts {
  display: flex;
  gap: 12px;
  margin-bottom: 24px;
}

.amounts button {
  flex: 1;
  padding: 12px;
  border: 1px solid #e5e7eb;
  background: white;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 600;
  transition: all 0.3s;
}

.amounts button:hover {
  border-color: #2563eb;
}

.amounts .active {
  background: #2563eb;
  color: white;
  border-color: #2563eb;
}

.donate-btn {
  width: 100%;
  background: #f97316;
  color: white;
  border: none;
  padding: 14px;
  border-radius: 8px;
  margin-bottom: 15px;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.3s;
}

.donate-btn:hover {
  background: #ea580c;
}


@media (max-width: 1024px) {
  .donation {
    padding: 80px 40px;
  }

  .donation h2 {
    font-size: 28px;
  }

  .donation-box {
    padding: 28px;
  }
}


@media (max-width: 768px) {
  .donation {
    padding: 60px 20px;
  }

  .donation h2 {
    font-size: 24px;
    margin-bottom: 10px;
  }

  .donation p {
    font-size: 14px;
    margin-bottom: 30px;
  }

  .donation-box {
    max-width: 100%;
    margin: 30px auto 0;
    padding: 24px;
  }

  .amounts {
    gap: 10px;
  }

  .amounts button {
    padding: 10px;
    font-size: 13px;
  }

  .donate-btn {
    padding: 12px;
    font-size: 14px;
  }
}

@media (max-width: 480px) {
  .donation {
    padding: 40px 16px;
  }

  .donation h2 {
    font-size: 20px;
  }

  .donation p {
    font-size: 13px;
  }

  .donation-box {
    padding: 20px;
  }

  .amounts {
    flex-direction: column;
    gap: 8px;
  }

  .amounts button {
    padding: 10px;
  }

  .donate-btn {
    padding: 12px;
  }
}