.final-cta {
  padding: 80px 20px;
  text-align: center;
}

.final-cta h2 {
  font-size: 32px;
  font-weight: 800;
  color: #0f172a;
  margin-bottom: 20px;
}

.final-cta p {
  font-size: 16px;
  color: #475569;
  margin-bottom: 40px;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.final-cta button {
  margin: 8px;
  padding: 14px 24px;
  border-radius: 6px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s;
  font-size: 14px;
}

.dark {
  background: #111827;
  color: white;
  border: none;
}

.dark:hover {
  background: #1f2937;
}

.light {
  border: 1px solid #d1d5db;
  background: white;
  color: #0f172a;
}

.light:hover {
  border-color: #2563eb;
  color: #2563eb;
}


@media (max-width: 1024px) {
  .final-cta {
    padding: 60px 40px;
  }

  .final-cta h2 {
    font-size: 28px;
  }

  .final-cta button {
    padding: 12px 20px;
  }
}


@media (max-width: 768px) {
  .final-cta {
    padding: 40px 20px;
  }

  .final-cta h2 {
    font-size: 24px;
    margin-bottom: 16px;
  }

  .final-cta p {
    font-size: 14px;
    margin-bottom: 32px;
  }

  .final-cta button {
    display: block;
    width: 100%;
    margin: 10px 0;
    padding: 12px 20px;
  }
}

@media (max-width: 480px) {
  .final-cta {
    padding: 30px 16px;
  }

  .final-cta h2 {
    font-size: 20px;
  }

  .final-cta p {
    font-size: 13px;
  }

  .final-cta button {
    margin: 8px 0;
    padding: 12px 16px;
    font-size: 13px;
  }
}