.trust {
  display: flex;
  justify-content: center;
  gap: 48px;
  padding: 48px 60px;
  border-top: 1px solid #e5e7eb;
  border-bottom: 1px solid #e5e7eb;
  flex-wrap: wrap;
}

.trust div {
  display: flex;
  align-items: center;
  gap: 10px;
  font-weight: 600;
  font-size: 14px;
  color: #0f172a;
}

.trust div svg {
  font-size: 20px;
  color: #2563eb;
  flex-shrink: 0;
}


@media (max-width: 1024px) {
  .trust {
    padding: 40px 40px;
    gap: 32px;
  }

  .trust div {
    font-size: 13px;
  }
}


@media (max-width: 768px) {
  .trust {
    flex-direction: column;
    gap: 20px;
    padding: 32px 20px;
  }

  .trust div {
    justify-content: flex-start;
    font-size: 13px;
  }
}

@media (max-width: 480px) {
  .trust {
    padding: 24px 16px;
    gap: 16px;
  }

  .trust div {
    font-size: 12px;
    gap: 8px;
  }

  .trust div svg {
    font-size: 18px;
  }
}