.solution {
  padding: 80px 60px;
  background: #ffffff;
}

.solution .label {
  font-size: 12px;
  font-weight: 700;
  color: #2563eb;
  letter-spacing: 0.1em;
  text-transform: uppercase;
}

.solution h2 {
  font-size: 32px;
  font-weight: 800;
  margin: 12px 0 40px;
  color: #0f172a;
}

.cards {
  display: flex;
  gap: 24px;
}

.card {
  flex: 1;
  padding: 32px;
  border-radius: 16px;
  transition: transform 0.3s, box-shadow 0.3s;
}

.card:hover {
  transform: translateY(-8px);
  box-shadow: 0 15px 40px rgba(0,0,0,0.1);
}

.card svg {
  font-size: 26px;
  margin-bottom: 16px;
}

.card h3 {
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 8px;
}

.card p {
  font-size: 14px;
  color: #475569;
  line-height: 1.6;
}

.card.blue {
  background: #eef2ff;
  color: #1d4ed8;
}

.card.orange {
  background: #fff7ed;
  color: #c2410c;
}

.card.green {
  background: #ecfdf5;
  color: #047857;
}


@media (max-width: 1024px) {
  .solution {
    padding: 60px 40px;
  }

  .solution h2 {
    font-size: 28px;
  }

  .cards {
    gap: 20px;
  }

  .card {
    padding: 24px;
  }

  .card h3 {
    font-size: 16px;
  }
}


@media (max-width: 768px) {
  .solution {
    padding: 40px 20px;
  }

  .solution h2 {
    font-size: 24px;
    margin-bottom: 30px;
  }

  .cards {
    flex-direction: column;
    gap: 16px;
  }

  .card {
    padding: 24px;
  }

  .card svg {
    font-size: 24px;
  }

  .card h3 {
    font-size: 16px;
  }

  .card p {
    font-size: 13px;
  }
}

@media (max-width: 480px) {
  .solution {
    padding: 30px 16px;
  }

  .solution h2 {
    font-size: 20px;
    margin-bottom: 20px;
  }

  .solution .label {
    font-size: 11px;
  }

  .card {
    padding: 20px;
  }

  .card svg {
    font-size: 22px;
    margin-bottom: 12px;
  }

  .card h3 {
    font-size: 15px;
    margin-bottom: 6px;
  }
}