.audience {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 24px;
  padding: 80px 60px;
}

.audience-card {
  border-radius: 16px;
  padding: 32px;
  transition: transform 0.3s, box-shadow 0.3s;
}

.audience-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 30px rgba(0,0,0,0.1);
}

.audience-card.blue {
  background: #f1f6ff;
}

.audience-card.dark {
  background: #111827;
  color: white;
}

.audience .icon {
  font-size: 28px;
  margin-bottom: 16px;
  color: #2563eb;
}

.audience .icon.dark {
  color: white;
}

.audience ul {
  margin: 24px 0;
  list-style: none;
  padding: 0;
}

.audience li {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 10px;
  font-size: 14px;
}

.outline {
  border: 1px solid #2563eb;
  background: transparent;
  padding: 10px 14px;
  border-radius: 6px;
  color: #2563eb;
  cursor: pointer;
  font-weight: 600;
  transition: all 0.3s;
}

.outline:hover {
  background: #f1f6ff;
}

.filled {
  background: white;
  color: #111827;
  border: none;
  padding: 10px 14px;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 600;
  transition: all 0.3s;
}

.filled:hover {
  background: #f1f6ff;
}


@media (max-width: 1024px) {
  .audience {
    padding: 60px 40px;
    gap: 20px;
  }

  .audience-card {
    padding: 24px;
  }
}

@media (max-width: 768px) {
  .audience {
    grid-template-columns: 1fr;
    padding: 40px 20px;
    gap: 16px;
  }

  .audience-card {
    padding: 24px;
  }

  .audience li {
    font-size: 13px;
  }
}

@media (max-width: 480px) {
  .audience {
    padding: 30px 16px;
  }

  .audience-card {
    padding: 20px;
  }

  .audience .icon {
    font-size: 24px;
  }

  .audience ul {
    margin: 16px 0;
  }

  .audience li {
    margin-bottom: 8px;
  }
}