.header {
  display: flex;
  justify-content: space-between;
  padding: 18px 60px;
  align-items: center;
  background: white;
  position: relative;
}

.logo {
  font-weight: 700;
  display: flex;
  align-items: center;
  font-size: 18px;
}

.logo-dot {
  color: #2563eb;
  margin-right: 8px;
}

nav {
  display: flex;
  gap: 16px;
  align-items: center;
}

nav a {
  margin: 0 16px;
  font-size: 14px;
  cursor: pointer;
  color: #0f172a;
  transition: color 0.3s;
}

nav a:hover {
  color: #2563eb;
}

.download-btn {
  background: #2563eb;
  color: white;
  border: none;
  padding: 10px 16px;
  border-radius: 6px;
  display: flex;
  gap: 8px;
  cursor: pointer;
  font-weight: 600;
  transition: background 0.3s;
}

.download-btn:hover {
  background: #1d4ed8;
}

.hamburger {
  display: none;
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
  color: #0f172a;
}

.desktop-only {
  display: flex;
}

nav .download-btn {
  display: none;
}


@media (max-width: 1024px) {
  .header {
    padding: 18px 40px;
  }

  nav a {
    margin: 0 12px;
  }
}


@media (max-width: 768px) {
  .header {
    padding: 16px 20px;
  }

  .logo {
    font-size: 16px;
  }

  nav {
    position: absolute;
    top: 60px;
    left: 0;
    width: 100%;
    background: white;
    flex-direction: column;
    gap: 0;
    padding: 20px;
    border-bottom: 1px solid #e5e7eb;
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.3s ease-in-out;
  }

  nav.active {
    max-height: 300px;
  }

  nav a {
    margin: 12px 0;
    padding: 10px 0;
    font-size: 16px;
  }

  .desktop-only {
    display: none;
  }

  .hamburger {
    display: block;
    z-index: 10;
  }

  nav .download-btn {
    width: 100%;
    justify-content: center;
    margin-top: 10px;
    display: flex;
  }
}

@media (max-width: 480px) {
  .header {
    padding: 12px 16px;
  }

  .logo {
    font-size: 14px;
  }
}