.hero {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 80px 60px;
  background: #ffffff;
  gap: 40px;
}

.hero-left {
  max-width: 520px;
  flex: 1;
}

.badge {
  display: inline-block;
  background: #eaf2ff;
  color: #2563eb;
  font-size: 12px;
  font-weight: 600;
  padding: 6px 12px;
  border-radius: 999px;
  margin-bottom: 24px;
}

.hero h1 {
  font-size: 44px;
  line-height: 1.2;
  font-weight: 800;
  color: #0f172a;
  margin-bottom: 20px;
}

.hero h1 span {
  color: #2563eb;
}

.hero p {
  font-size: 15px;
  color: #475569;
  line-height: 1.6;
  margin-bottom: 32px;
}

.actions {
  display: flex;
  gap: 16px;
  flex-wrap: wrap;
}

.actions button {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 14px 18px;
  border-radius: 8px;
  font-weight: 600;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.3s;
}

.actions .primary {
  background: #2563eb;
  color: white;
  border: none;
}

.actions .primary:hover {
  background: #1d4ed8;
}

.actions .secondary {
  background: transparent;
  border: 1px solid #e5e7eb;
  color: #0f172a;
}

.actions .secondary:hover {
  border-color: #2563eb;
  color: #2563eb;
}

.hero-right {
  width: 420px;
  height: 360px;
  border-radius: 16px;
  background-image: url("../../assets/images/unity.png");
  background-size: cover;
  background-position: center;
  position: relative;
  flex-shrink: 0;
}

.image-overlay {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 20px;
  background: linear-gradient(to top, rgba(0,0,0,0.6), transparent);
  color: white;
  font-weight: 600;
  font-size: 14px;
}


@media (max-width: 1024px) {
  .hero {
    padding: 60px 40px;
  }

  .hero h1 {
    font-size: 36px;
  }

  .hero-right {
    width: 350px;
    height: 300px;
  }
}


@media (max-width: 768px) {
  .hero {
    flex-direction: column;
    padding: 40px 20px;
    gap: 30px;
  }

  .hero-left {
    width: 100%;
    max-width: 100%;
  }

  .hero h1 {
    font-size: 28px;
    line-height: 1.3;
  }

  .hero p {
    font-size: 14px;
  }

  .actions {
    gap: 12px;
  }

  .actions button {
    padding: 12px 16px;
    font-size: 13px;
  }

  .hero-right {
    width: 100%;
    height: 280px;
  }
}

@media (max-width: 480px) {
  .hero {
    padding: 30px 16px;
    gap: 20px;
  }

  .hero h1 {
    font-size: 24px;
    margin-bottom: 16px;
  }

  .badge {
    font-size: 11px;
  }

  .actions {
    flex-direction: column;
    gap: 10px;
  }

  .actions button {
    width: 100%;
    justify-content: center;
    padding: 12px 14px;
  }

  .hero-right {
    height: 240px;
  }
}