.how {
  background: #f8fafc;
  padding: 80px 60px;
  text-align: center;
}

.how h2 {
  font-size: 28px;
  font-weight: 800;
  color: #0f172a;
}

.how p {
  margin-top: 8px;
  font-size: 14px;
  color: #64748b;
}

.steps {
  display: flex;
  justify-content: space-between;
  margin-top: 60px;
  position: relative;
  gap: 20px;
}

.steps::before {
  content: "";
  position: absolute;
  top: 30px;
  left: 0;
  width: 100%;
  height: 2px;
  background: #e5e7eb;
  z-index: 0;
}

.steps div {
  background: white;
  width: 120px;
  height: 120px;
  border-radius: 50%;
  border: 3px solid #e5e7eb;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-weight: 600;
  font-size: 13px;
  z-index: 1;
  flex: 1;
  max-width: 120px;
  margin: 0 auto;
}

.steps div svg {
  font-size: 24px;
  margin-bottom: 8px;
  color: #2563eb;
}


@media (max-width: 1024px) {
  .how {
    padding: 60px 40px;
  }

  .how h2 {
    font-size: 24px;
  }

  .steps {
    margin-top: 50px;
  }

  .steps div {
    width: 100px;
    height: 100px;
    font-size: 12px;
    max-width: 100px;
  }

  .steps div svg {
    font-size: 20px;
  }
}


@media (max-width: 768px) {
  .how {
    padding: 40px 20px;
  }

  .how h2 {
    font-size: 22px;
  }

  .how p {
    font-size: 13px;
  }

  .steps {
    flex-direction: column;
    margin-top: 40px;
    gap: 30px;
  }

  .steps::before {
    display: none;
  }

  .steps div {
    width: 100%;
    max-width: 100%;
    height: auto;
    min-height: 100px;
    margin: 0;
    border-radius: 12px;
    border: 2px solid #e5e7eb;
  }
}

@media (max-width: 480px) {
  .how {
    padding: 30px 16px;
  }

  .how h2 {
    font-size: 20px;
  }

  .steps {
    gap: 20px;
  }

  .steps div {
    min-height: 90px;
  }

  .steps div svg {
    font-size: 20px;
    margin-bottom: 6px;
  }
}